<template>
    <el-container>
        <el-header>
            <container-top></container-top>
        </el-header>
        <el-container>
            <nav-header></nav-header>
            <el-main>
                <el-container class="container-main-div">
                    <div class="container-div">
                        <div class="container-main">
                            <div class="top-main">
                                <div class="page-top-line">
                                    <div class="page-title">数据管理</div>
                                </div>
                                <div class="subbtn">
                                    <div v-for="(item,index) in tabList" :class="activeClass==tabList[index].sceneName?'active':''" @click="changeBtn(tabList[index].sceneName)">
                                        <el-badge :value="tabList[index].institution" class="item">
                                            <p>{{tabList[index].sceneName}}</p>
                                        </el-badge>
                                    </div>
                                </div>
                                <div class="screen-block">
                                    <el-button v-if="tabList.length>0" class="submit-btn-style" @click="handleAdd" icon="el-icon-plus">新增数据集</el-button>
                                </div>
                            </div>
                            <div class="out-div">
                                <div class="account-list">
                                    <template>
                                        <el-table :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%">
                                            <el-table-column prop="datasetName.datasetName" label="数据集名称"></el-table-column>
                                            <el-table-column prop="alignedNum.alignedNum" label="对齐样本量"></el-table-column>
                                            <el-table-column prop="providers" label="数据提供方">
                                                <template slot-scope="scope">
                                                    <div>
                                                        <p v-for="(item,index) in scope.row.providers">{{item.provider}}</p>
                                                    </div>
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="timestamp" label="建立时间"></el-table-column>
                                            <el-table-column label="操作" width="240px">
                                                <template slot-scope="scope">
                                                    <div class="cell">
                                                        <!-- <div @click="handleEdit(scope.$index, scope.row)" class="btns edit-btn">
                                                            <img src="static/images/edit.png" alt="">
                                                            <p>数据编辑</p>
                                                        </div> -->
                                                        <div @click="handleDelete(scope.$index, scope.row)" class="btns del-btn">
                                                            <img src="static/images/del.png" alt="">
                                                            <p>删除</p>
                                                        </div>
                                                    </div>
                                                </template>
                                            </el-table-column>
                                        </el-table>
                                        <el-pagination
                                            @size-change="handleSizeChange"
                                            @current-change="handleCurrentChange"
                                            :current-page="currentPage"
                                            :page-sizes="[5,10,20,50]"
                                            :page-size="pagesize"
                                            layout="total, sizes, prev, pager, next, jumper"
                                            :total="tableData.length"
                                            style="margin-top:15px">
                                        </el-pagination>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-container>
                <el-dialog title="" :visible.sync="dialogVisibleDel" width="400px" :lock-scroll="false" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
                    <img src="static/images/del_tip.png" class="del-tip-style" alt="">
                    <p class="del-tip-p">确定删除？</p>
                    <el-form ref="formDel" :model="formDel" label-width="80px">
                        <el-form-item label="ID" style="display:none">
                            <el-input v-model="formDel.datasetName"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="deleteThis">确 定</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleDel = false">取 消</el-button>
                    </span>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>
</template>
<style>
    @import "../../static/css/dataManage.css";
</style>
<script>
import $ from "jquery";
import Header from "../sub/aside";
import containerTop from "../sub/container_top";
export default {
    name: "datamanage",
    components: {
        "nav-header": Header,
        "container-top": containerTop,
    },
    data: function() {
        return {
            username:Cookies.get("fedname"),
            token:Cookies.get("fedtoken"),
            tabList:[],
            activeClass:'',

            tableData: [],
            pagesize:10,
            currentPage:1,

            // 删除弹窗
            dialogVisibleDel:false,
            formDel:{},
        }
    },
    mounted(){
        this.findDimensions()
        this.getSceneList()
    },
    methods: {
        // 数据集列表
        getDataList:function(){
            var that = this;
            that.axios.post('/datasetReq',that.qs.stringify({
                "scene": that.activeClass,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.tableData=response.data.datasets
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },

        // 新增数据集
        handleAdd:function(){
            var that=this;
            that.$router.push('/dataWork?sceneName='+that.activeClass)
        },

        // 删除
        handleDelete(index, rowData) {
            this.dialogVisibleDel = true;
            this.formDel= Object.assign({}, rowData);
        },
        deleteThis:function(){
            var that=this;
            that.dialogVisibleDel=false;
            that.axios.post('/datasetDelete',that.qs.stringify({
                "scene":that.activeClass,
                "datasetName":that.formDel.datasetName.datasetName,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.$message({
                        message: '删除数据集成功',
                        type: 'success'
                    });
                    that.currentPage=1;
                    that.getDataList()
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },

        // 修改
        // handleEdit:function(index, rowData){
        //     var that=this;
        //     that.$router.push('/dataWork?sceneName='+that.activeClass+'&datasetName='+rowData.datasetName.datasetName)
        // },

        // 获取场景列表
        getSceneList:function(){
            var that = this;
            that.axios.post('/sceneReq',that.qs.stringify({
                "specific":false,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    if(response.data.scenes.length){
                        that.tabList=response.data.scenes
                        that.activeClass=response.data.scenes[0].sceneName;
                        that.getDataList()
                    }
                    else {
                        that.tabList=[];
                        that.activeClass="";
                    }
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        changeBtn:function(id){
            var that=this;
            that.activeClass=id
            that.getDataList()
        },

        handleSizeChange: function (size) {
            this.pagesize = size;
        },
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage;
        },
        findDimensions:function(){
            if(window.innerWidth)
            this.winWidth = window.innerWidth;
            else if ((document.body) && (document.body.clientWidth))
            this.winWidth = document.body.clientWidth;
            //获取窗口高度
            if(window.innerHeight)
            this.winHeight = window.innerHeight;
            else if((document.body) && (document.body.clientHeight))
            this.winHeight = document.body.clientHeight;
            //通过深入Document内部对body进行检测，获取窗口大小
            if(document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth){
                this.winHeight = document.documentElement.clientHeight;
                this.winWidth = document.documentElement.clientWidth;
            }
            $(".account-list").attr("style","min-height:"+(this.winHeight-420)+"px;")
        },
    }
};
</script>



